"use client";

import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Link from "next/link";
import { LayoutDashboard, FileText, TrendingUp, Settings, LogOut } from "lucide-react";

export default function AdminLayout({ children }: { children: React.ReactNode }) {
    const { user, isLoading, logout } = useAuth();
    const router = useRouter();

    useEffect(() => {
        if (!isLoading) {
            if (!user || !user.is_admin) {
                router.push("/");
            }
        }
    }, [user, isLoading, router]);

    if (isLoading) {
        return <div className="flex items-center justify-center min-h-screen">Loading...</div>;
    }

    if (!user || !user.is_admin) {
        return null;
    }

    return (
        <div className="flex min-h-screen bg-gray-900 text-white">
            {/* Sidebar */}
            <aside className="w-64 bg-gray-800 border-r border-gray-700 p-6 flex flex-col">
                <div className="mb-8">
                    <h1 className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-400">
                        Admin Panel
                    </h1>
                </div>

                <nav className="flex-1 space-y-2">
                    <Link href="/admin" className="flex items-center gap-3 px-4 py-3 rounded-xl hover:bg-gray-700 transition-colors text-gray-300 hover:text-white">
                        <LayoutDashboard className="w-5 h-5" />
                        Dashboard
                    </Link>
                    <Link href="/admin/stocks" className="flex items-center gap-3 px-4 py-3 rounded-xl hover:bg-gray-700 transition-colors text-gray-300 hover:text-white">
                        <TrendingUp className="w-5 h-5" />
                        Manage Stocks
                    </Link>
                    <Link href="/admin/blogs" className="flex items-center gap-3 px-4 py-3 rounded-xl hover:bg-gray-700 transition-colors text-gray-300 hover:text-white">
                        <FileText className="w-5 h-5" />
                        Manage Blogs
                    </Link>
                </nav>

                <div className="mt-auto pt-6 border-t border-gray-700">
                    <button
                        onClick={logout}
                        className="flex items-center gap-3 px-4 py-3 w-full rounded-xl hover:bg-red-900/20 text-red-400 hover:text-red-300 transition-colors"
                    >
                        <LogOut className="w-5 h-5" />
                        Sign Out
                    </button>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 p-8 overflow-auto">
                {children}
            </main>
        </div>
    );
}
